import React from 'react'
import { Row, Col } from 'antd'
import styled from 'styled-components'
import Footer from '@/components/app-footer'
import { useBreadcrumb } from '@/hooks/useApp'
import useAuth from '@/hooks/useAuth'
import envSotre from '@/constants/env'
import history from '@/utils/history'
import { REPORT_LIST, RADIOL_REPORT_LIST } from '@/constants/routes'

const Block = styled.div`
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 320px;
  padding: 32px 60px 32px 52px;
  border-radius: 20px;
  background: linear-gradient(
    to bottom,
    ${p => p.theme.primary},
    ${p => p.theme.secondary}
  );
  color: #fff;
  font-size: 29px;
  font-weight: 500;
  text-align: center;
  cursor: pointer;

  svg {
    margin-right: 36px;
  }
`

const HomePage = styled.div`
  height: 100vh;
  background-color: #38a8a9;
  background-image: url('${envSotre.publicUrl}/images/bg-home.jpg');
  background-size: cover;
  background-position: center;
  opacity: 0.8;
`

export default function Home() {
  useBreadcrumb([{ name: '首頁' }])

  const { user } = useAuth()

  const handleTo = (pathname: string) => () => {
    history.push(pathname)
  }

  const showBg = user.division === 'PATH' || user.division === 'RAD'

  return (
    <>
      {showBg && <HomePage />}
      {!showBg && (
        <div style={{ height: 'calc(100vh - 204px)' }}>
          <Row>
            <Col span={24} className="text-center">
              <div css={{ marginTop: 200 }}>
                <Block
                  onClick={handleTo(RADIOL_REPORT_LIST)}
                  style={{ marginRight: 100 }}>
                  放射科
                </Block>
                <Block onClick={handleTo(REPORT_LIST)}>病理科</Block>
              </div>
            </Col>
          </Row>
        </div>
      )}
      <Footer />
    </>
  )
}
